.footer__menu-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  width: 68.4%;
  // min-width: 460px;
  // justify-content: space-between;
  // border: 2px solid red;
  // justify-content: flex-end;

  @include vp-1023 {
    width: 533px;
  }

  @include vp-767 {
    display: block;
    width: 100%;
    height: 120px;
    column-count: 2;
    column-gap: 30px;
    margin-bottom: 17px;
  }
}

.footer__menu-item {
  margin-right: 40px;
  margin-top: -2px;

  @include vp-1023 {
    margin-top: -6px;
    margin-right: 30px;
  }

  @include vp-767 {
    // width: 48%;
    margin-right: 0;
    margin-bottom: 17px;
    margin-top: 0;
  }
}


.footer__menu-item.footer__menu-item--promo {
  @include vp-767 {
    // margin-top:40px;
    // border: 2px solid red;
  }
}

.footer__menu-item a {
  @include font($ff-roboto, 400, 14px, $color-default-white, normal);

  @include vp-1023 {}
  @include vp-767 {
    font-size: 16px;
  }
}

.footer__menu-item--schedule {
  margin-right: 0;
}
